function tableHead() {
	if ( Modernizr.touchevents  && $(window).width()<600){
		$(".fixTable").tableHeadFixer({"head": false, "left":0});
	} else {
		$(".fixTable").tableHeadFixer({"head": true, "left": 1});
	}
}
